.main-footer {
  color: @black;
  background-color: @white;
}

.main-footer__copyright {
  display: flex;
}


.main-footer-container {
  min-height: 127px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-around;
  }
}
